<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  mounted() {
    this.$global.appendChild()
  },
  watch: {
    $route() {
      this.$global.routeWatch()
    }
  }
}
</script>

<style>
@import './assets/css/reset.css';
#app{
  font-size: 24px;
  height: 100%;
  background: #eee;
}
</style>



